import { Vector } from "../core/Vector";
export var calcCameraRotationMatrixV1 = function (eye, center, up) {
    var f = Vector.normalize(Vector.subtract(center, eye));
    var s = Vector.normalize(Vector.cross(f, up));
    var u = Vector.cross(s, f);
    return [
        [s.x, u.x, -f.x, 0],
        [s.y, u.y, -f.y, 0],
        [s.z, u.z, -f.z, 0],
        [-Vector.dot(s, eye), -Vector.dot(u, eye), Vector.dot(f, eye), 1]
    ];
};
export var calcCameraRotationMatrixV2 = function (position, direction, up) {
    if (up === void 0) { up = { x: 0, y: 1, z: 0 }; }
    var right = Vector.normalize(Vector.cross(up, direction));
    var newUp = Vector.normalize(Vector.cross(direction, right));
    return [
        [right.x, newUp.x, direction.x, position.x],
        [right.y, newUp.y, direction.y, position.y],
        [right.z, newUp.z, direction.z, position.z],
        [0, 0, 0, 1]
    ];
};
